/* FONTES - CORES*/
$cor-header: #042A8F;
$cor-section1: #778DA9;
$cor-botoes: #C33C54;
$cor-background: #dfdddd;
$fonte-menu: 'Gurajada', serif;
$fonte-titulo: 'Marcellus SC', serif;
$fonte-corpo: 'Thasadith', sans-serif;

/* RESET - CONFIGURAÇÃO */
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box
}

html {
    scroll-behavior: smooth;
}

img {
    width: 100%;
    height: auto;
}

a {
    text-decoration: none;
}

ul, li {
    list-style: none;
}

/* MIXINS */
@mixin titulo-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
    background-color: $cor-header;
    padding: 1rem;
}



/* PÁGINA - ARQUITETURA */
@import "header";
@import "slider-promo";
@import "promocao";
@import "alimentos";
